
.DateInput {
  font-weight: 200;
  font-size: 18px;
  line-height: 24px;
  color: #757575;
  margin: 0;
  padding: 0;

  background: white;
  position: relative;
  display: inline-block;
  width: 130px;
  vertical-align: middle;
}
.DateInput__withCaret:before {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: auto;
  border: 10px solid transparent;
  left: 22px;
  z-index: 2;
}
.DateInput__withCaret:after {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: auto;
  border: 10px solid transparent;
  left: 22px;
  z-index: 2;
}
.DateInput__openUp:before {
  border-bottom: 0;
  top: -24px;
  border-top-color: rgba(0, 0, 0, 0.1);
}
.DateInput__openUp:after {
  border-bottom: 0;
  top: -25px;
  border-top-color: white;
}
.DateInput__openDown:before {
  border-top: 0;
  top: 62px;
  border-bottom-color: rgba(0, 0, 0, 0.1);
}
.DateInput__openDown:after {
  border-top: 0;
  top: 63px;
  border-bottom-color: white;
}
.DateInput__disabled {
  background: #f2f2f2;
  color: #dbdbdb;
}
.DateInput_input {
  font-weight: 200;
  font-size: 18px;
  color: #565a5c;
  background: white;
  width: 100%;
  padding: 12px 12px;
  border: 0;
  border-top: 0;
  border-right: 0;
  border-bottom: 2px solid transparent;
  border-left: 0;
}
.DateInput_input__readOnly {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.DateInput_input__focused {
  outline: 0;
  background: white;
  border: 0;
  border-top: 0;
  border-right: 0;
  border-bottom: 2px solid #008489;
  border-left: 0;
}
.DateInput_input__disabled {
  background: #f2f2f2;
  font-style: italic;
}
.DateInput_screenReaderMessage {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}



